.wrapper {
  height: 100vh;
  width: 98.7vw;
  display: flex;
  justify-content: center;
  align-items: center;
}
.Uploaded {
  height: 25vh;
  width: 30vw;
  display: flex;
  flex-direction: column;
  justify-content: space-evenly;
  align-items: center;
  background: #ffffff;
  box-shadow: 0px 4px 12px rgba(0, 0, 0, 0.1);
  border-radius: 12px;
}
.Uploaded h2 {
  font-family: Poppins;
  font-style: normal;
  font-weight: 500;
  font-size: 18px;
  line-height: 27px;
  letter-spacing: -0.035em;
  color: #4f4f4f;
}
.bar {
  height: 7px;
  width: 80%;
  overflow: hidden;
  background: #f2f2f2;
  border-radius: 8px;
}

@keyframes bar_loading {
  0% {
    transform: translateX(-10%);
  }
  50% {
    transform: translateX(270px);
  }
  100% {
    transform: translateX(-10%);
  }
}

.Item__bar {
  background: #2f80ed;
  height: 100%;
  width: 20%;
  border-radius: 8px;
  animation: bar_loading 2s ease-in-out infinite;
}

@media screen and (max-width: 425px) {
  .Uploaded {
    height: 35vh;
    width: 90vw;
    display: flex;
    flex-direction: column;
    justify-content: space-evenly;
    align-items: center;
    background: #ffffff;
    box-shadow: 0px 4px 12px rgba(0, 0, 0, 0.1);
    border-radius: 12px;
  }
}
