.wrapper {
  height: 87vh;
  width: 90vw;
  box-sizing: border-box;
  padding-top: 15px;
  display: flex;
  background-color: #e3e1dc;
  flex-direction: column;
  justify-content: flex-start;
  align-items: center;
  gap: 43px;
  border-bottom-left-radius: 34px;
  border-bottom-right-radius: 34px;
}
.top {
  width: 80%;
  height: 31%;
  display: flex;
  margin-top: 15px;
}
.top__left {
  height: 100%;
  width: 50%;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
}
.top__left h2 {
  font-family: Montserrat;
  font-style: normal;
  font-weight: bold;
  font-size: 48px;
  line-height: 59px;
  color: #291507;
}
.top__left p {
  font-family: Montserrat;
  font-style: normal;
  font-weight: 500;
  font-size: 18px;
  line-height: 22px;
  color: #291507;
}
.top__left p::after {
  content: '';
  display: block;
  height: 4px;
  background: #4d270c;
  border-radius: 4px 4px 0px 0px;
  transition: all 300ms ease-in-out 0s;
  width: 10%;
}
.top__right {
  height: 100%;
  width: 50%;
  display: flex;
  justify-content: flex-end;
  align-items: flex-end;
}
.top__right p {
  cursor: pointer;
  font-family: Montserrat;
  font-style: normal;
  font-weight: bold;
  font-size: 18px;
  line-height: 22px;
  text-align: right;
  color: rgba(41, 21, 7, 0.6);
}

.bottom {
  width: 80%;
  height: 50%;
  display: grid;
  grid-template-columns: repeat(4, 22%);
  grid-template-rows: 80%;
  gap: 37px;
}
.bottomItem {
  display: flex;
  flex-direction: column;
}
.bottomItem p {
  font-family: Montserrat;
  font-style: normal;
  font-weight: 600;
  font-size: 18px;
  line-height: 22px;
  color: #291507;
}
.bottomItem__Img {
  height: 100%;
  width: 100%;
  border-radius: 24px;
  overflow: hidden;
}
.bottomItem__Img img {
  max-height: 100%;
  min-height: 100%;
  max-width: 100%;
  cursor: pointer;
  object-fit: cover;
}
@media screen and (max-width: 425px) {
  .wrapper {
    width: 95vw;
    box-sizing: border-box;
    padding-top: 15px;
    display: flex;
    background-color: #e3e1dc;
    flex-direction: column;
    justify-content: flex-start;
    align-items: center;
    gap: 43px;
    border-bottom-left-radius: 34px;
    border-bottom-right-radius: 34px;
  }
  .top__left p {
    font-size: 12px;
    line-height: 15px;
  }
  .top__left h2 {
    font-size: 18px;
    line-height: 22px;
  }
  .top {
    height: 15%;
  }
  .bottom {
    width: 85%;
    height: 70%;
    display: grid;
    grid-template-columns: repeat(2, 45%);
    grid-template-rows: 45%;
    gap: 20px;
  }
  .bottomItem p {
    font-size: 12px;
    line-height: 15px;
  }
  .discover_bottomItem__Img__v-9S5 img {
    min-width: 100%;
  }
  .top__left p::after {
    width: 22%;
  }
}
